'use client';

import React, { useState } from 'react';
import {
  Box,
  Avatar,
  Typography,
  Divider,
  Toolbar,
  IconButton,
  Tooltip,
  Collapse,
  Chip,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import {
  Dashboard,
  PeopleAlt,
  Info,
  ExpandLess,
  ExpandMore,
  Logout,
  Person,
  AssignmentInd,
} from '@mui/icons-material';
import { useRouter } from 'next/navigation';
import { useUser } from '@/contexts/UserContext';

const Sidebar = () => {
  const { user, logout } = useUser();
  const router = useRouter();
  const [openCollapse, setOpenCollapse] = useState(false);

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  const handleNavigation = (path: string) => {
    router.push(path);
  };

  return (
    <Box
      sx={{
        width: 260,
        height: '100vh',
        bgcolor: '#0A0F19',
        color: '#fff',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        px: 2,
        py: 3,
        boxShadow: '2px 0 8px rgba(0, 0, 0, 0.5)',
      }}
    >
      <Box>
        <Toolbar sx={{ mb: 2 }} />
        {user ? (
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              textAlign: 'center',
              mb: 3,
            }}
          >
            <Avatar
              src={user.profilePicture}
              alt={user.fullName}
              sx={{
                width: 72,
                height: 72,
                mb: 1,
                border: '2px solid #00ED64',
              }}
            />
            <Typography variant="h6" fontWeight={600} color="#00ED64">
              {user.fullName}
            </Typography>
            <Typography variant="body2" color="gray">
              {user.email}
            </Typography>
            <Chip
              label={user.role}
              size="small"
              sx={{
                mt: 1,
                bgcolor: '#1a1f2b',
                color: '#00ED64',
                fontWeight: 'bold',
              }}
            />
          </Box>
        ) : (
          <Typography variant="body2" align="center" color="gray">
            Loading user info...
          </Typography>
        )}

        <Divider sx={{ mb: 2, borderColor: '#222' }} />

        <List component="nav" disablePadding>
          <Tooltip title="Dashboard" placement="right">
            <ListItemButton
              sx={{
                mb: 1,
                borderRadius: 2,
                color: '#fff',
                '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
              }}
              onClick={() => handleNavigation('/dashboard')}
            >
              <ListItemIcon sx={{ color: 'inherit' }}>
                <Dashboard />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItemButton>
          </Tooltip>

          <Tooltip title="Manage Users" placement="right">
            <ListItemButton
              sx={{
                mb: 1,
                borderRadius: 2,
                color: '#fff',
                '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
              }}
              onClick={() => handleNavigation('/manage-users')}
            >
              <ListItemIcon sx={{ color: 'inherit' }}>
                <PeopleAlt />
              </ListItemIcon>
              <ListItemText primary="Manage Users" />
            </ListItemButton>
          </Tooltip>

          <Tooltip title="More Options" placement="right">
            <ListItemButton
              onClick={() => setOpenCollapse(!openCollapse)}
              sx={{
                mb: 1,
                borderRadius: 2,
                color: '#fff',
                '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
              }}
            >
              <ListItemIcon sx={{ color: 'inherit' }}>
                <AssignmentInd />
              </ListItemIcon>
              <ListItemText primary="More" />
              {openCollapse ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
          </Tooltip>

          <Collapse in={openCollapse} timeout="auto" unmountOnExit>
            <Box pl={4}>
              <ListItemButton
                sx={{
                  mb: 1,
                  borderRadius: 2,
                  color: '#fff',
                  '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
                }}
                onClick={() => handleNavigation('/profile')}
              >
                <ListItemIcon sx={{ color: 'inherit' }}>
                  <Person />
                </ListItemIcon>
                <ListItemText primary="My Profile" />
              </ListItemButton>

              <ListItemButton
                sx={{
                  mb: 1,
                  borderRadius: 2,
                  color: '#fff',
                  '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
                }}
                onClick={() => handleNavigation('/about')}
              >
                <ListItemIcon sx={{ color: 'inherit' }}>
                  <Info />
                </ListItemIcon>
                <ListItemText primary="About Company" />
              </ListItemButton>
            </Box>
          </Collapse>
        </List>
      </Box>

      <Box>
        <Divider sx={{ mb: 2, borderColor: '#222' }} />
        <Tooltip title="Logout" placement="right">
          <ListItemButton
            onClick={handleLogout}
            sx={{
              borderRadius: 2,
              color: '#fff',
              '&:hover': { bgcolor: '#00ED64', color: '#0A0F19' },
            }}
          >
            <ListItemIcon sx={{ color: 'inherit' }}>
              <Logout />
            </ListItemIcon>
            <ListItemText primary="Logout" />
          </ListItemButton>
        </Tooltip>
        <Typography
          variant="caption"
          color="gray"
          sx={{ mt: 2, textAlign: 'center', display: 'block' }}
        >
          v1.0.0 — Employee Portal
        </Typography>
      </Box>
    </Box>
  );
};

export default Sidebar;
