'use client';

import React from 'react';
import AppToolbar from './AppToolbar';
import Sidebar from './Sidebar';
import AppFooter from './AppFooter';
import Template from './Template';
import { Box, Typography, Grid, Card, CardContent, Button } from '@mui/material';
import { useUser } from '@/contexts/UserContext'; // ✅ Added import

const primaryColor = '#00ED64';     // MongoDB Green
const secondaryColor = '#0A0F19';   // Dark Background

export default function DashboardPage() {
  return (
    <Template>
      <Sidebar />
      <main
        style={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          minHeight: '100vh',
          backgroundColor: secondaryColor,
          color: 'white',
        }}
      >
        <AppToolbar />

        <Box sx={{ flex: 1, p: 4 }}>
          <Typography variant="h4" gutterBottom sx={{ color: primaryColor }}>
            Welcome to Admin Dashboard
          </Typography>

          {/* The rest of your existing content remains unchanged */}
        </Box>

        <AppFooter />
      </main>
    </Template>
  );
}
